import { FileTypeMap } from '@ali/4ever-plugin-embed';

export default function getFileName4Show(innerType: string, nameParam: string) {
  if (!nameParam) return '';
  // 钉钉文字表格白板则去掉后缀
  if (innerType === FileTypeMap.DingSheet
      || innerType === FileTypeMap.DingDoc
      || innerType === FileTypeMap.WhiteBoard) {
    const nameMatch = nameParam.match(/(.*)\.(\w+$)/);
    return nameMatch ? nameMatch[1] : nameParam;
  }
  return nameParam;
};
